* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    width: 100%;
    font-size: 16px;
}

.App-mobile-logo {
    display: none;
}

.App-header {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    margin: auto;
    -webkit-transition: all 0.8s;
    -o-transition: all 0.8s;
    transition: all 0.8s;
}

.App-header-first-step {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    margin: auto;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
}

.App-header-last-step {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    margin: auto;
    -webkit-transition: all 0.8s;
    -o-transition: all 0.8s;
    transition: all 0.8s;
}

.App-header::after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,0.45)), color-stop(65%, rgba(0,0,0,0.00)));
    background: -webkit-linear-gradient(bottom, rgba(0,0,0,0.45) 0%, rgba(0,0,0,0.00) 65%);
    background: -o-linear-gradient(bottom, rgba(0,0,0,0.45) 0%, rgba(0,0,0,0.00) 65%);
    background: linear-gradient(0deg, rgba(0,0,0,0.45) 0%, rgba(0,0,0,0.00) 65%);
}

.App-header-image {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    padding: 20px 20px 20px 30px;
    -webkit-transition: all 0.8s;
    -o-transition: all 0.8s;
    transition: all 0.8s;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    opacity: 0;
    background-image: url("/assets/images/booster.png"), url("/assets/images/scents.png"), url("/assets/images/product"), url("/assets/images/focusArea.png"), url("/assets/images/oils.png"), url("/assets/images/butters.png"), url("/assets/images/recipient.png"),
}

.App-header-text {
    font-size: 50px;
}

.selection-panel {
    width: 55%;
    -ms-flex: 1 1;
        flex: 1 1;
}

.modal-container {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
    -webkit-transition-delay: .1s;
         -o-transition-delay: .1s;
            transition-delay: .1s;
    z-index: 2;
}

.modal {
    position: relative;
    width: 50%;
    margin: 20vh auto;
    background-color: white;
    border-radius: 5px;
}

.modal-heading {
  text-align: center;
  padding: 20px 20px 0;
}

.modal-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 100%;
    padding: 5% 10% 15%;
}

.modal-message {
  /*align-items: center;
  justify-content: center;*/
  width: 100%;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.modal-btn-container {
    /*position: absolute;
    bottom: 0;*/
    width: 100%;
    text-align: center;
    /*overflow: auto;*/
    -ms-flex-item-align: end;
        align-self: flex-end;
}

.modal-img {
    /*margin: 0px auto;
    height: 400px;
    position: relative;
    top: -50px;
    left: 0;
    right: 0;*/
    -ms-flex-item-align: center;
        align-self: center;
}

.properties-top, .properties-bottom {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: baseline;
        align-items: baseline;
    width: 50%;
    margin: 40px auto;
    text-align: center;
    font-weight: 300;
}

.properties-bottom {
    width: 60%;
}

.property-img-container {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
}

.landing-msg {
    text-align: center;
    padding: 30px 5% 0;
}

@media all and (max-width: 1060px) {

    .properties-top {
        width: 80%;
    }
    
    .properties-bottom {
      width: 90%;
    }
}

@media all and (max-width: 768px) {
    .App-mobile-logo {
        display: block;
        -webkit-box-shadow: 0 7px 10px 0 rgba(0,0,0,0.06);
                box-shadow: 0 7px 10px 0 rgba(0,0,0,0.06);
    }
    
    /*.modal-btn-container {
        padding-bottom: 120px;
