        font-size: 30px;
    }

    .selection-panel {
        width: 100%;
    }

    .side-panel {
        display: none;
    }
    
    .modal {
      position: fixed;
      width: 100%;
      height: 100vh;
      margin: 0;
      border-radius: 0;
    }
    
    .modal-img {
      margin-top: 10%;
