        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        height: 100%;
        padding: 5% 10% 120px;
    }
    
    .properties-top, .properties-bottom {
        width: 300px;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-direction: row;
            flex-direction: row;
    }
    
    .property {
