    }
    
    .properties-bottom {
      margin-top: 0;
    }
    
    .property-img-container {
        margin-top: 20px;
    }
    
    .landing-img {
       background-position: -15%
    }
    
    .landing-btn {
        margin-bottom: 170px;
    }
} 

@media all and (max-width: 600px) {

  .properties-top img, .properties-bottom img {
      width: 40px;
      height: 40px;
  }
  
  .properties-top, .properties-bottom {
      margin: 0;
      width: 100%;
  }
  
  .properties-top {
    margin-top: 0;
  }
  
  .property-img-container {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .property {
    font-size: 16px;
  }
  
  .landing-msg {
      padding: 15px 5%;
  }
  
}

@media all and (max-width: 400px) {
    .App-header-text {
        font-size: 24px;
    }
    
    .modal-img {
      bottom: 120px;
    }
    
    /*.modal-btn-container {
      padding-bottom: 60px;
    }*/
}



